.navbar {
  background-color: #fff !important;
 }
 
 
.navbar-brand {
 font-size: 1.9rem !important;
 color: #565387;
 }

 .navbar {
  background-color: #fff !important;
 }
 
 .navbar a {
  font-size: 1.3rem !important;
 text-transform: capitalize;
 }

 .menu_active {
  font-weight: bold;
  border-bottom:  1px solid #565387;
 }

 
 .navbar a:hover {
