 }
 

 #header  {
 width: 100%;
 height: 80vh;
 margin-top: 70px;
 }

 
 #header h1 {
 margin: 0 0 10px 0;
 font-size: 48px;
 font-weight: 700;
 color: black;
  }
 
  
 #header .brand-name  {
  color: #3498db;
  }

  
 #header h2 {
  
  font-size: 24px;
  
  color: #484848;
   }

   
 #header .btn-get-started {
  
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
  
   }



   
 #header .btn-get-started:hover {
  background: #3498db;
  color: #fff;
   } 

   
   .header-img{
   text-align: right;
     } 

     #header .animated{
      animation: up-down 2s ease-in-out infinite alternate alternate-reverse both;
        } 

.header-img {
  -webkit-animation: mover 1s infinite  alternate;
  animation: mover 1s infinite  alternate;
}
.header-img {
  -webkit-animation: mover 1s infinite  alternate;
  animation: mover 1s infinite  alternate;
}
@-webkit-keyframes mover {
  0% { transform: translateY(0); }
  100% { transform: translateY(-45px); }
}
@keyframes mover {
  0% { transform: translateY(0); }
  100% { transform: translateY(-45px); }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.card-img-top {
  height: 15rem;
  object-fit: cover;
}